    /**
     * Validates wether the query corresponds to the model.
     * @param options Validate options.
     * @returns `true` if it is valid, otherwise `false`
     */
    tryValidate(options) {
        try {
            this.validate(options);
            return true;
        }
        catch (e) {
            if (e instanceof InvalidQueryError) {
                return false;
            }
            else {
                throw e;
            }
        }
    }
    validateConditionGroup(curCond, options) {
        if (curCond.isGroup()) {
            for (let condition of curCond.getConditions()) {
                if (!condition.enabled)
                    continue;
                this.validateConditionGroup(condition, options);
            }
        }
        else if (curCond.expressions[0].tag === ExprTag.EntityAttribute) {
            const attrId = curCond.expressions[0].value;
            const attr = this.model.getAttributeById(attrId);
            if (!attr) {
                throw new InvalidQueryError("EntityAttribute isn't declared in the model: " + attrId);
            }
            if (!options.checkExpressions)
                return;
            for (let i = 1; i < curCond.expressions.length; i++) {
                const expr = curCond.expressions[i];
                if (expr.tag !== ExprTag.Constant)
                    continue;
                //validate number exprs
                if (utils.isNumericType(expr.dataType)) {
                    if (!expr.value)
                        continue;
                    let values = [];
                    if (expr.kind === DataKind.List) {
                        values = expr.value.split(/\s*,\s*/);
                    }
                    else {
                        values = [expr.value];
                    }
                    for (let value of values) {
                        const parsedVal = +value;
                        if (isNaN(parsedVal)) {
                            throw new InvalidQueryError("Expression constant is NaN in condition:" + curCond.id);
                        }
                        if (utils.isIntType(expr.dataType) && parsedVal != parseInt(value, 10)) {
                            throw new InvalidQueryError("Expression constant is wrong interger in condition: " + curCond.id);
                        }
                    }
                }
            }
        }
    }
