    /**
     * Loads query from data representation.
     * @param data The query DTO.
     * @param options The options.
     * @throws {InvalidQueryError} The error throws when query is not valid.
     */
    loadFromData(data, options) {
        options = utils.assign({
            changeStatus: true,
            validate: true
        }, options || {});
        let noErrors = false;
        this.clear();
        this.clearDrillDowns();
        if (data) {
            this.id = data.id;
            this.root.loadFromData(this.model, data.root);
            if (data.root && data.root.conds) {
                for (let i = 0; i < data.root.conds.length; i++) {
                    const condData = data.root.conds[i];
                    if (condData.conds && !this.isEx()) {
                        console.error("Community version doesn't support working with groups.");
                        continue;
                    }
                    const cond = this.createCondition();
                    cond.loadFromData(this.model, condData);
                    this.root.addCondition(cond);
                }
            }
            this.name = data.name;
            if (data.desc) {
                this.description = data.desc;
            }
            this.sortedColumns = [];
            if (data.justsortcols) {
                for (let i = 0; i < data.justsortcols.length; i++) {
                    let newCol = this.createColumn(true);
                    newCol.loadFromData(this.model, data.justsortcols[i]);
                    //this.justsorted.push(newCol);
                    this.addColumnToSorted(newCol);
                }
            }
            if (data.cols) {
                for (let i = 0; i < data.cols.length; i++) {
                    let newCol = this.createColumn();
                    newCol.loadFromData(this.model, data.cols[i]);
                    this.columns.push(newCol);
                    if (newCol._sorting != SortDirection.None) {
                        this.addColumnToSorted(newCol);
                    }
                }
            }
            this.sortedColumns.sort((col1, col2) => {
                return col1._sortIndex - col2._sortIndex;
            });
            this.fixupSortingIndices();
            if (data.extraData) {
                this.extraData = data.extraData;
            }
            this.innerData = data.innerData || this.innerData;
            if (this.innerData && this.innerData.aggr) {
                this._enableAggregation = this.innerData.aggr.enabled || false;
                this.useAggregation(settings => {
                    settings.loadFromData(this.innerData.aggr);
                }, true);
            }
            if (data.dds && data.dds.length) {
                for (let dd of data.dds) {
                    const query = this.addDrillDown();
                    query.loadFromData(dd);
                }
            }
        }
        if (this._enableAggregation) {
            this.updateAggregationSettings();
        }
        noErrors = true;
        if (noErrors) {
            if (options.changeStatus) {
                this.isNewbie = false;
                this._isModified = false;
            }
        }
        if (options.validate) {
            this.validate();
        }
    }
