        // }
        this.sortedColumns.forEach(col => {
            if (col.isJustSorted()) {
                obj.justsortcols.push(col.saveToData());
            }
        });
        if (this.extraData) {
            obj.extraData = this.extraData;
        }
        if (this.innerData) {
            obj.innerData = this.innerData;
            obj.innerData.aggr = this.aggrSettings.saveToData();
            obj.innerData.aggr.enabled = this._enableAggregation;
        }
        const rootCondToData = (condGroup) => {
            const result = condGroup.saveToData();
            result.conds = [];
            const conditions = condGroup.getConditions();
            for (let i = 0; i < conditions.length; i++) {
                if (conditions[i].isGroup() && !this.isEx()) {
                    console.error("Community version doesn't support working with groups.");
                    continue;
                }
                result.conds.push(conditions[i].saveToData());
            }
            return result;
        };
        obj.root = rootCondToData(this.root);
        obj.extraConds = rootCondToData(this.extraConditions);
        if (this.drillDowns.length > 0) {
            obj.dds = [];
            for (let dd of this.drillDowns) {
                obj.dds.push(dd.toJSONData());
            }
        }
        obj.timezoneOffset = this.timezoneOffset;
        obj.locale = i18n.getCurrentLocale();
        return obj;
