                newColumn.expr.tag = ExprTag.AggregateFunction;
                newColumn.expr.func = descriptor.aggrFuncId;
                newColumn.expr.kind = DataKind.Scalar;
                newColumn.expr.dataType = funcId == 'COUNT' || funcId == 'CNTDST' ? 4 : attr.dataType;
                newColumn.expr.distinct = false;
                const arg = new Expression(newColumn);
                arg.setValue(attr.id, true);
                arg.tag = ExprTag.EntityAttribute;
                arg.kind = DataKind.Attribute;
                arg.dataType = attr.dataType;
