    /**
     * Changes column to Aggregate function, custom expression or simple column.
     * @param column The column to change.
     * @param tag New column expression tag.
     * @param args The arguments (Contains aggregate function id)
     */
    changeColumnType(column, tag, args) {
        if (column.isJustSorted())
            return;
        args = args || {};
        if (tag === ExprTag.AggregateFunction) {
            if (!args.funcId)
                throw Error('"funcId" argument is not defined');
            if (column.expr.tag == ExprTag.AggregateFunction) {
                column.expr.func = args.funcId;
                return;
            }
            const expr = new Expression(column);
            expr.tag = ExprTag.AggregateFunction;
            expr.func = args.funcId;
            expr.distinct = false;
            expr.dataType = args.funcId == 'COUNT' || args.funcId == 'CNTDST' ? 4 : column.expr.dataType;
            const arg = new Expression(column);
            arg.loadFromData(this.model, column.expr.saveToData());
            expr.args.push(arg);
            column.expr = expr;
        }
        else if (tag === ExprTag.CustomSql) {
            const attrId = (column.expr.tag == ExprTag.AggregateFunction)
                ? column.expr.args[0].value
                : column.expr.value;
            let sqlText = '';
            if (this.model) {
                sqlText = this.model.getAttributeById(attrId).expr;
            }
            const customSqlExpr = new Expression(column);
            customSqlExpr.tag = ExprTag.CustomSql;
            customSqlExpr.dataType = DataType.String; //the default data type
            customSqlExpr.sql = sqlText;
            customSqlExpr.baseAttrId = attrId;
            column.expr = customSqlExpr;
        }
        else if (tag === ExprTag.EntityAttribute) {
            if (column.expr.tag === ExprTag.EntityAttribute
                || column.expr.tag === ExprTag.ParentEntityAttribute) {
                return;
            }
            const attrId = (column.expr.tag == ExprTag.AggregateFunction)
                ? column.expr.args[0].value
                : column.expr.baseAttrId;
            const attr = attrId
                ? this.model.getAttributeById(attrId)
                : this.model.getFirstUICAttr();
            const expr = new Expression(column);
            expr.tag = ExprTag.EntityAttribute;
            expr.dataType = attr.dataType;
            expr.setValue(attr.id, true);
            column.expr = expr;
        }
        column.caption = this.context.getDefaultColumnCaption(column);
    }
