    /**
     * Gets an array of all entities used in the query.
     */
    getEntitiesInQuery() {
        if (this.model.id !== this.id)
            throw Error('Model mithmatch');
        const entities = [];
        const columns = this.getColumns();
        for (let column of columns) {
            if (!column.enabled)
                continue;
            this.addEntityByExpression(entities, column.expr);
        }
        const rootCond = this.getRootCondition();
        this.fillEntitiesByCondtionGroup(entities, rootCond);
        return entities;
    }
    fillEntitiesByCondtionGroup(entities, root) {
        for (let condition of root.getConditions()) {
            if (!condition.isGroup()) {
                for (let expr of condition.expressions) {
                    this.addEntityByExpression(entities, expr);
                    if (expr.args) {
                        for (let arg of expr.args) {
                            this.addEntityByExpression(entities, arg);
                        }
                    }
                }
            }
            else {
                this.fillEntitiesByCondtionGroup(entities, condition);
            }
        }
    }
    addEntityByExpression(entities, expr) {
        if (expr.tag !== ExprTag.EntityAttribute)
            return;
        const attrId = expr.baseAttrId || expr.value;
        if (attrId) {
            const attr = this.model.getAttributeById(attrId);
            if (attr) {
                if (attr.entity && utils.indexOfArrayItem(entities, attr.entity) < 0) {
                    entities.push(attr.entity);
                }
            }
        }
    }
    /**
     * Builds and returns the path (actually the tree) between the entities used in the query.
     */
    buildQueryPath() {
        let entity = this.model.getMainEntity() || this.model.rootEntity;
        const tree = this.createTreeNodeFromEntity(entity);
        tree.setParents();
        return tree;
    }
    createTreeNodeFromEntity(entity) {
        let tree = new Tree(entity);
        for (let subEnentity of entity.subEntities) {
            tree.addChild(this.createTreeNodeFromEntity(subEnentity));
        }
        return tree;
    }
