class BrowserQueryStorage {
    constructor(context) {
        this.context = context;
        console.warn('BrowserQueryStorage is used for managing queries');
    }
    init(options) {
        let keyPrefix = 'eq';
        if (options && options.keyPrefix) {
            keyPrefix = options.keyPrefix;
        }
        this.queryListKey = keyPrefix + '-querylist';
        this.queryKeyPrefix = keyPrefix;
    }
    getQueryList(options) {
        return new Promise((resolve, reject) => {
            try {
                const itemsString = localStorage.getItem(this.queryListKey);
                if (itemsString) {
                    resolve(JSON.parse(itemsString));
                }
                resolve([]);
            }
            catch (err) {
                reject(err);
            }
        });
    }
    newQuery(options) {
        return new Promise((resolve, reject) => {
            const query = new Query(this.context.getModel(), null, { context: this.context });
            query.reset();
            query.setDefaultName();
            if (options) {
                if (options.query) {
                    query.loadFromData(options.query.toJSONData());
                }
                if (options.queryId) {
                    query.setId(options.queryId);
                }
                if (options.name) {
                    query.setName(options.name);
                }
                if (options.description) {
                    query.setDescription(options.description);
                }
            }
            this._saveQuery(query);
            resolve(query.toJSONData());
        });
    }
    loadQuery(options) {
        return new Promise((resolve, reject) => {
            try {
                const queryItem = localStorage.getItem(this.queryKeyPrefix + '-' + options.queryId);
                if (queryItem) {
                    resolve(JSON.parse(queryItem));
                }
                throw new Error('Query is not found: ' + options.queryId);
            }
            catch (err) {
                reject(err);
            }
        });
    }
    saveQuery(options) {
        return new Promise((resolve, reject) => {
            try {
                const modelId = options.modelId || this.context.getModel().getId();
                const query = options.query || this.context.getQuery();
                this._saveQuery(query);
                resolve(null);
            }
            catch (err) {
                reject(err);
            }
        });
    }
    _saveQuery(query) {
        let queryList = [];
        const itemsString = localStorage.getItem(this.queryListKey);
        if (itemsString) {
            queryList = JSON.parse(itemsString);
        }
        let item = utils.findItemById(queryList, query.getId());
        if (!item) {
            item = { id: query.getId(), name: query.getName(), text: query.getDescription() };
            queryList.push(item);
        }
        item.name = query.getName();
        item.text = query.getDescription();
        localStorage.setItem(this.queryListKey, JSON.stringify(queryList));
        localStorage.setItem(this.queryKeyPrefix + '-' + query.getId(), query.toJSON());
    }
    removeQuery(options) {
        return new Promise((resolve, reject) => {
            try {
                const modelId = options.modelId || this.context.getModel().getId();
                const queryId = options.queryId || this.context.getQuery().getId();
                let queryList = [];
                const itemsString = localStorage.getItem(this.queryListKey);
                if (itemsString) {
                    queryList = JSON.parse(itemsString);
                }
                queryList = queryList.filter(item => item.id != queryId);
                localStorage.setItem(this.queryListKey, JSON.stringify(queryList));
                localStorage.removeItem(this.queryKeyPrefix + '-' + queryId);
                resolve();
            }
            catch (err) {
                reject(err);
            }
        });
    }
}

