class EqAggregatesContainer {
    constructor(context) {
        this.context = context;
        this.levels = {};
    }
    getAggregateData(level, groupKey) {
        return new Promise((resolve, reject) => {
            const processFunc = (data) => {
                // map values
                const keyStr = this.groupKeyToStr(groupKey);
                const value = data.get(keyStr);
                if (value) {
                    resolve(value);
                }
                else {
                    reject(`Can't find data for ${keyStr}`);
                }
            };
            const desc = this.getLevelDesc(level, false);
            if (desc) {
                if (desc.data) {
                    processFunc(desc.data);
                }
                else {
                    desc.receivedCallbacks.push(processFunc);
                }
            }
        });
    }
    setAggregateData(level, data) {
        const desc = this.getLevelDesc(level, true);
        desc.data = data;
        if (desc.receivedCallbacks) {
            for (const callback of desc.receivedCallbacks) {
                callback(desc.data);
            }
        }
        desc.receivedCallbacks = [];
    }
    updateAggregateData(level, groupKey, values) {
        const desc = this.getLevelDesc(level, true);
        const key = this.groupKeyToStr(groupKey);
        desc.data.set(key, values);
    }
    groupKeyToStr(groupKey) {
        return JSON.stringify(groupKey);
    }
    getLevelDesc(level, createIfNotExists) {
        let desc = this.levels[level];
        if (!desc && createIfNotExists) {
            desc = {
                receivedCallbacks: [],
                data: new Map()
            };
            this.levels[level] = desc;
        }
        return desc;
    }
    clear() {
        this.levels = {};
    }
}
