class EqServerExporter {
    constructor(context) {
        this.context = context;
    }
    setFormat(format, responseType) {
        this.format = format;
        this.responseType = responseType;
    }
    export(exportOptions) {
        exportOptions = exportOptions || {};
        const http = this.context.getServices().getHttpClient();
        const format = this.format || "csv";
        const query = exportOptions.query || this.context.getQuery();
        const modelId = query.getModel().getId();
        let requestData = {
            'settings': exportOptions.settings,
            'chunk': EmptyChunk
        };
        if (typeof (exportOptions.queryId) !== 'undefined') {
            requestData.queryId = exportOptions.queryId;
        }
        else {
            requestData.query = query.toJSONData();
        }
        if (exportOptions.data) {
            requestData.data = exportOptions.data;
        }
        if (exportOptions.options) {
            requestData.options = exportOptions.options;
        }
        const url = this.context.resolveEndpoint('ExportResult', { modelId: modelId, format: format });
        const actionResult = http.post(url, requestData, { responseType: this.responseType });
        const request = actionResult.getRequest();
        return actionResult.then((responseData) => {
            const blob = new Blob([responseData]);
            const dispositionHeader = request.getXMLHttpRequest().getResponseHeader("Content-Disposition");
            const filename = dispositionHeader
                ? dispositionHeader.match(/filename="(.*)"/)[1]
                : 'export-data.' + format;
            if (window.navigator['msSaveOrOpenBlob']) {
                // Internet Explorer
                window.navigator['msSaveOrOpenBlob'](blob, filename);
            }
            else {
                var a = document.createElement("a");
                document.body.appendChild(a);
                a.style.display = "none";
                a.href = window.URL.createObjectURL(blob);
                a.download = filename;
                a.click();
                window.URL.revokeObjectURL(a.href);
                document.body.removeChild(a);
            }
        });
    }
}

class EasyQueryDataLoader {
    constructor(context) {
        this.context = context;
    }
    loadChunk(chunk) {
        // We have to refactor page logic in view to make it simplier to integrate 
        // different grids
        this.dataLoadPromise = new Promise((resolve, reject) => {
            const fetchOptions = {
                chunk: chunk,
                success: (result) => {
                    const dataTable = new EasyDataTable({
                        loader: this.context.dataLoader, //TODO: Why not just `this`?
                        chunkSize: this.context.resultTable.chunkSize
                    });
                    const resultSet = result.resultSet;
                    for (const col of resultSet.cols) {
                        dataTable.columns.add(col);
                    }
                    dataTable.setTotal(result.meta.totalRecords);
                    for (const row of resultSet.rows) {
                        dataTable.addRow(row);
                    }
                    let totalRecords = 0;
                    if (result.meta && result.meta.totalRecords) {
                        totalRecords = result.meta.totalRecords;
                    }
                    resolve({
                        table: dataTable,
                        total: totalRecords,
                        hasNext: !chunk.needTotal || chunk.offset + chunk.limit < totalRecords
                    });
                },
                error: (error) => reject(error)
            };
            if (this.context.debugMode) {
                fetchOptions.debug = `Data Chunk. Offset: ${chunk.offset}`;
            }
            this.context.fetchDataChunk(fetchOptions);
        });
        return this.dataLoadPromise;
    }
}

