    highlightText(content, text) {
        if (text && text.length > 0) {
            const lowerText = text.toLowerCase();
            const lowerContent = content.toLowerCase();
            const insertValue1 = `<span class='eqjs-menu-itemDiv-highlight'>`;
            const insertValue2 = `</span>`;
            const startIndex = lowerContent.indexOf(lowerText);
            if (startIndex >= 0) {
                let pos = 0;
                const value = content;
                const indexInMas = [];
                while (true) {
                    const index = lowerContent.indexOf(lowerText, pos);
                    if (index >= 0) {
                        indexInMas.push(index);
                        pos = index + text.length;
                    }
                    else {
                        pos++;
                    }
                    if (pos >= value.length - 1) {
                        break;
                    }
                }
                if (indexInMas.length > 0) {
                    content = '';
                    for (let i = 0; i < indexInMas.length; i++) {
                        if (i === 0) {
                            content += value.substring(0, indexInMas[i]);
                        }
                        content += insertValue1 + value.substring(indexInMas[i], indexInMas[i] + text.length) + insertValue2;
                        if (i < indexInMas.length - 1) {
                            content += value.substring(indexInMas[i] + text.length, indexInMas[i + 1]);
                        }
                        else {
                            content += value.substring(indexInMas[i] + text.length);
                        }
                    }
                }
            }
        }
        return content;
    }
