class ListExpressionRenderer extends ExpressionRenderer {
    constructor() {
        super(...arguments);
        this.menuItemsList = [];
        this.showWhenReady = false;
    }
    isEditable() {
        return !this.isReadOnly()
            && ((this.panel.options.editableParts & QPEditablePart.Value) === QPEditablePart.Value);
    }
    setValue(value, silent = false) {
        const newValue = this.adjustNewValue(value);
        if (value !== this.expression.value) {
            const text = this.getValueText(value);
            this.expression.setContent(newValue, text);
            if (!silent) {
                this.expression.getParent().fireChangedEvent(ConditionPart.Value);
            }
        }
        return true;
    }
    getValueText(value) {
        let result = "";
        if (Array.isArray(value)) {
            for (let item of this.menuItemsList) {
                if (value.indexOf(item.id) >= 0) {
                    result += item.text + ',';
                }
            }
        }
        else {
            for (let item of this.menuItemsList) {
                if (item.id === value) {
                    result += item.text + ',';
                }
            }
        }
        if (result) {
            result = result.substring(0, result.length - 1);
        }
        return result;
    }
    getEmptyText() {
        return this.panel.options.emptyTextList || i18n.getText('MsgEmptyListValue');
    }
    getListName() {
        return this.valueEditor.name;
    }
    renderEditor() {
        this.fillMenuItemsList(() => {
            this.renderMenuBlock();
        });
    }
    getLabelText() {
        if (!this.expression.isEmpty()) {
            return this.expression.getText();
        }
        else {
            return this.getEmptyText();
        }
    }
    showLoader() {
        //NOTHING TO DO
    }
    hideLoader() {
        //NOTHING TO DO
    }
    showEditor() {
        if (this.menu) {
            this.menu.showMenu({
                anchor: this.labelElement,
                selectedIds: this.getValuesAsArray(),
                itemSelectedCallback: (item, selectedItems) => {
                    if (!this.menu.options.multiselect) {
                        this.setValue(item.id);
                    }
                    else if (selectedItems) {
                        let selectedIds = [];
                        let selLength = selectedItems.length;
                        for (let i = 0; i < selLength; i++)
                            selectedIds.push(selectedItems[i].id);
                        this.setValue(selectedIds);
                    }
                    return false;
                }
            });
        }
        else {
            this.showWhenReady = true;
            this.showLoader();
        }
    }
    closeEditor() {
        this.menu.hideMenu();
    }
    renderMenuBlock() {
        let multiSelect = false;
        if (this.expression) {
            multiSelect = this.expression.kind === DataKind.List;
        }
        const queryPanel = this.panel;
        const options = {
            items: this.menuItemsList,
            multiselect: multiSelect,
            container: queryPanel ? queryPanel.options.menuContainer : null,
            showSelected: true,
            showItemIds: queryPanel ? queryPanel.options.showIdsForListItems : false
        };
        if (queryPanel) {
            utils.assignDeep(options, queryPanel.options.menuOptions);
        }
        let menuId = this.element.id;
        if (menuId) {
            options.id = menuId + '-EditorMenu';
        }
        if (queryPanel) {
            options.domWriteItemsId = queryPanel.options.domWriteItemsId;
        }
        options.buttons = {
            submit: i18n.getText('ButtonApply'),
            cancel: i18n.getText('ButtonCancel')
        };
        this.menu = new PopupMenu(options);
    }
    getValuesAsArray() {
        if (this.expression.kind !== DataKind.List) {
            return [this.expression.value];
        }
        else {
            if (this.expression.value) {
                //splits value by ',' 
                const res = this.expression.value.match(/"[^"\\]*(?:\\.[^"\\]*)*"|[^,]+/g);
                return res
                    ? res.map(a => {
                        if (a.charAt(0) == '"' && a.charAt(a.length - 1) == '"') {
                            a = a.substring(1, a.length - 1);
                            return a.replace(/\"/g, '"');
                        }
                        else {
                            return a;
                        }
                    })
                    : [];
            }
            else
                return [];
        }
    }
    adjustNewValue(newValue) {
        if (Array.isArray(newValue)) {
            return newValue.map((a) => {
                if (a.indexOf(',') >= 0) {
                    a = a.replace(/"/g, '\"');
                    return '"' + a + '"';
                }
                else {
                    return a;
                }
            }).join(',');
        }
        else {
            return newValue;
        }
    }
    fillMenuItemsList(onComplete) {
        this.menuItemsList = this.valueEditor.values;
        if (onComplete) {
            onComplete();
        }
    }
    takeDefaultValue() {
        if (!this.menuItemsList)
            return;
        let itemLen = this.menuItemsList.length;
        let defValue = this.valueEditor.defValue;
        for (let i = 0; i < itemLen; i++) {
            let item = this.menuItemsList[i];
            if (item.isDefault || defValue == item.id) {
                this.setValue(item.id, true);
                break;
            }
        }
    }
}
