/**
 * The view that represents a data-filtering scenario (a text search for one entity/table)
 */
class DataFilterView extends View {
    constructor() {
        super();
    }
    initOptions(options) {
        options = super.initOptions(options);
        options.result.fetchEmptyQueries = true;
        const query = this.context.getQuery();
        query.addChangedCallback(() => {
            if (this.resultOptions.clearResultOnQueryChange) {
                this.context.clearResult();
            }
            if (options.syncQueryOnChange) {
                this.context.syncQuery();
            }
        });
        this.context.addEventListener('ready', this.fetchData.bind(this));
        return options;
    }
    initWidgets(options) {
        super.initWidgets(options);
        if (this.sortingBarWidget) {
            this.context.getQuery().addChangedCallback((ev) => {
                const edata = ev.data;
                if (!edata || (edata.part != QueryChangePart.SortingColumns
                    && edata.part != QueryChangePart.Columns && edata.part != QueryChangePart.All)) {
                    return;
                }
                this.fetchData();
            });
        }
        if (this.filterBarWidget) {
            const widgetType = this.filterBarWidget.getWidgetType();
            options.widgets[widgetType] = options.widgets[widgetType] || {};
            if (!options.widgets[widgetType].applyFilterCallback) {
                options.widgets[widgetType].applyFilterCallback = () => {
                    this.fetchData();
                };
            }
        }
    }
    /**
     * Obsolete. Left for backward compatibility.
     * Use fetchData() instead
     * @deprecated Use `fetchData()`
     */
    applyQueryFilter() {
        console.warn('applyQueryFilter is obsolete. Use fetchData instead');
        this.fetchData();
    }
    fetchData() {
        super.fetchData({ refresh: true });
    }
}

